import { Bar } from 'vue-chartjs'
export default Bar.extend({
  props: ['temps', 'label', 'labels', 'color'],
  mounted() {
    // Overwriting base render method with actual data.
    this.renderChart({
      labels: this.labels,
      datasets: [
        {
          label: this.label,
          backgroundColor: this.color,
          data: this.temps
        }
      ]
    })
  },created(){
    // console.log(this.temps);
  }
})


